(function() {
  'use strict';

  angular
    .module('catwalk', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize',
      'ngMessages', 'ngAria', 'ui.router', 'ngMaterial', 'toastr',
      'firebase']);

})();

(function() {
'use strict';

angular
.module('catwalk')
.service('Catwalks', ['firebaseRoot', function(firebaseRoot){
  return firebaseRoot.child("catwalk");
}])

})();

(function() {

'use strict';

angular
.module('catwalk')
.directive('catwalkFields', function(){
  return {
    restrict: 'E',
    scope: {
      catwalk: '='
    },
    templateUrl: 'app/components/catwalk/catwalk.fields.directive.html'
  }
})

})();

(function() {

'use strict';

angular
.module('catwalk')
.config(['$stateProvider', function($stateProvider) {
  $stateProvider
  .state('splash', {
    url: '/',
    templateUrl: 'app/splash/splash.html'
  })
}]);

})();

(function() {

'use strict';

angular
.module('catwalk')
.config(['$stateProvider', function($stateProvider) {
  $stateProvider
  .state('dash', {
    url: '/dash',
    templateUrl: 'app/dash/dash.html',
    controller: function($scope, $timeout, $firebaseArray, Catwalks){
      $scope.newCatwalk = {};

      $scope.catwalks = $firebaseArray(Catwalks)
      $scope.catwalks.$loaded().then(function(){
        $scope.catwalksLoaded = true
        $scope.$watch('catwalks.length', function(){
          if ($scope.catwalks.length==0) { $scope.addingCatwalk = true }
        });
      });

      $scope.createCatwalk = function(){
        Catwalks.push($scope.newCatwalk, function(){
          $timeout(function(){ $scope.addingCatwalk = false })
          $scope.newCatwalk={}
        });
      }

      $scope.delete = function(catwalk){
        Catwalks.child(catwalk.$id).remove();
      }
    }
  })
}]);

})();

(function() {
'use strict';

angular
.module('catwalk')

.config(['$urlRouterProvider', '$mdThemingProvider', function($urlRouterProvider,$mdThemingProvider){
  $urlRouterProvider.otherwise('/');

  $mdThemingProvider.theme('default')
    .primaryPalette('indigo', {
      default: '900'
    })
    .accentPalette('yellow', {
      default: '800',
    })
    .warnPalette('red', {
      default: 'A700'
    })
}])

.constant('firebaseUrl', 'https://catwalk-sample.firebaseio.com')

.service('firebaseRoot', ['Firebase', 'firebaseUrl', function(Firebase, firebaseUrl){
  return new Firebase(firebaseUrl);
}])

})();

